'use strict';

import { CameraView, useCameraPermissions } from 'expo-camera';
import moment from 'moment';
import { Box, Button, Center, HStack, Text, VStack } from 'native-base';
import React, { useEffect, useState } from 'react';
import { Alert, Dimensions, StyleSheet } from 'react-native';
import { OrderItem } from '../types/OrderItem';

interface ScanScreenProps {
  latestScan: OrderItem | null;
  setLatestScan: (scan: OrderItem | null) => void;
  order: OrderItem[];
  setOrder: (order: OrderItem[]) => void;
}

interface JsonToRealtextMap {
  [key: string]: string;
}

const jsonToRealtextMap: JsonToRealtextMap = {
  customer: 'Kund',
  skruvabArticleNumber: 'Skruvab art nr',
  customerArticleNumber: 'Kund art nr',
  name: 'Benämning',
  amount: 'Antal',
  location: 'Plats',
  date: 'Datum',
  misc: 'Övrigt',
};

const ScanScreen: React.FC<ScanScreenProps> = ({ latestScan, setLatestScan, order, setOrder }) => {
  const [permission, requestPermission] = useCameraPermissions();
  const [isScanEnabled, setIsScanEnabled] = useState(true);
  const [amountMultiplier, setAmountMultiplier] = useState(1);
  const [currentScanIndex, setCurrentScanIndex] = useState<number | null>(null);

  if (!permission) {
    return <Center flex={1}><Text>Requesting camera permission...</Text></Center>;
  }
  if (!permission.granted) {
    return <Center flex={1}><Button onPress={requestPermission}>Bevilja tillgång till kamera</Button></Center>;
  }

  return (
    <Box flex={1}>
      <Box height={Dimensions.get('window').height}>
        <CameraView
          style={StyleSheet.absoluteFillObject}
          barcodeScannerSettings={{ barcodeTypes: ['qr'] }}
          onBarcodeScanned={isScanEnabled ? ({ data }) => {
            setIsScanEnabled(false);
            try {
              const scannedData = JSON.parse(data) as Partial<OrderItem>;
              const { skruvabArticleNumber, amount, location } = scannedData;

              if (skruvabArticleNumber && amount && location) {
                setCurrentScanIndex(null);
                setLatestScan(scannedData as OrderItem);
                order.forEach((item, index) => {
                  if (
                    item.skruvabArticleNumber === skruvabArticleNumber &&
                    item.amount === amount &&
                    item.location === location
                  ) {
                    setCurrentScanIndex(index);
                    setAmountMultiplier(item.amountMultiplier);
                    setLatestScan({ ...item, amountMultiplier: item.amountMultiplier });
                  }
                });
              }
            } catch (err) {
              Alert.alert(
                'Fel inträffade',
                'Lyckades inte läsa QR-kod. Försök igen och om det återuppstår felsök innehållet i QR-koden.',
                [
                  {
                    text: 'Okej',
                    style: 'cancel',
                    onPress: () => setIsScanEnabled(true),
                  },
                ],
              );
            }
          } : undefined}
        />
      </Box>
      {latestScan && (
        <Box position="absolute" top={0} left={0} right={0} backgroundColor="rgba(0,0,0,0)">
          <Box p={4}>
            <Box bg="white" rounded="lg" shadow={2}>
              <VStack space={3} p={4}>
                {(Object.keys(jsonToRealtextMap) as Array<keyof typeof jsonToRealtextMap>).map(key => (
                  <HStack key={key} justifyContent="space-between" alignItems="center">
                    <Text fontWeight="medium">{jsonToRealtextMap[key]}</Text>
                    <Text>{String(latestScan[key] || '')}</Text>
                  </HStack>
                ))}
                <HStack space={2} mt={2}>
                  <Button
                    flex={1}
                    colorScheme="red"
                    onPress={() => {
                      setAmountMultiplier(1);
                      setLatestScan(null);
                      setIsScanEnabled(true);
                    }}>
                    <Text color="white">Avbryt</Text>
                  </Button>
                  <Button
                    flex={1}
                    colorScheme="green"
                    onPress={() => {
                      const newOrder = [...order];
                      if (currentScanIndex !== null) {
                        newOrder[currentScanIndex] = {
                          ...latestScan,
                          amountMultiplier,
                        };
                      } else {
                        newOrder.push({
                          ...latestScan,
                          amountMultiplier,
                          timestamp: moment().valueOf(),
                        });
                      }
                      setAmountMultiplier(1);
                      setOrder(newOrder);
                      setLatestScan(null);
                      setIsScanEnabled(true);
                    }}>
                    <Text color="white">Spara</Text>
                  </Button>
                </HStack>
                {currentScanIndex !== null && (
                  <>
                    <Text color="red" fontWeight="bold" textAlign="center">
                      Denna artikel har redan skannats innan. 
                    </Text>
                    <Text color="red" fontWeight="bold" textAlign="center">
                      Antal är den totala beställningen.
                    </Text>
                    </>
                  )}
                <HStack justifyContent="space-between" alignItems="center">
                  <Text fontWeight="medium">Antal</Text>
                  <HStack space={2} alignItems="center">
                    <Button
                      size="sm"
                      onPress={() => setAmountMultiplier(prev => Math.max(1, prev - 1))}
                      >
                      <Text color="white">-</Text>
                    </Button>
                    <Text>{amountMultiplier}</Text>
                    <Button
                      size="sm"
                      onPress={() => setAmountMultiplier(prev => prev + 1)}
                      >
                      <Text color="white">+</Text>
                    </Button>
                  </HStack>
                </HStack>
              </VStack>
            </Box>
          </Box>
        </Box>
      )}
    </Box>
  );
};

const styles = StyleSheet.create({
  cameraContainer: {
    ...StyleSheet.absoluteFillObject,
  },
});

export default ScanScreen;
