import React, { useState } from 'react';
import { View, StyleSheet, Alert } from 'react-native';
import { Button, Text } from 'native-base';
import AsyncStorage from '@react-native-async-storage/async-storage';
import * as DocumentPicker from 'expo-document-picker';
import * as FileSystem from 'expo-file-system'; // For robust file reading

// Key for storing the raw XML string
const RAW_XML_TEMPLATE_STORAGE_KEY = '@RawXMLTemplateString';

export default function SettingsView() {
  const [isLoading, setIsLoading] = useState(false);

  const handlePickAndStoreFileAsString = async () => {
    setIsLoading(true);
    try {
      const pickerResult = await DocumentPicker.getDocumentAsync({
        type: ['application/xml', 'text/xml', 'application/octet-stream', '*/*'],
        copyToCacheDirectory: true,
      });

      if (!pickerResult.canceled && pickerResult.assets && pickerResult.assets.length > 0) {
        const fileAsset = pickerResult.assets[0];
        const fileUri = fileAsset.uri;
        const fileName = fileAsset.name;

        let fileContentString: string | null = null;
        try {
          // Read the entire file content as a string
          fileContentString = await FileSystem.readAsStringAsync(fileUri, {
            encoding: FileSystem.EncodingType.UTF8, // Assuming UTF-8 encoding for XML
          });
        } catch (readError) {
          console.error('Failed to read file content:', readError);
          Alert.alert('Fel', 'Kunde inte läsa innehållet i den valda filen.');
          setIsLoading(false);
          return;
        }

        if (fileContentString === null || typeof fileContentString !== 'string') {
          Alert.alert('Fel', 'Kunde inte hämta innehållet från filen.');
          setIsLoading(false);
          return;
        }

		if (!fileContentString.includes('<REPLACE_THIS/>')) {
		  Alert.alert('Fel', 'XML-mallen innehöll inte platshållare för order: <REPLACE_THIS/>');
          setIsLoading(false);
          return;
		}

        // --- Store the raw XML string ---
        try {
          // No parsing here, just store the string directly
          await AsyncStorage.setItem(RAW_XML_TEMPLATE_STORAGE_KEY, fileContentString);
          Alert.alert(
            'XML-mall sparad!',
            `Innehållet från filen "${fileName}" har sparats som en textmall.`
          );
        } catch (storageError) {
          console.error('Failed to save XML string to storage:', storageError);
          Alert.alert('Lagringsfel', 'Kunde inte spara XML-mallen.');
        }

      } else if (pickerResult.canceled) {
        console.log('User cancelled the document picker.');
      } else {
        Alert.alert('Fel', 'Ingen fil valdes eller så uppstod ett oväntat fel.');
      }
    } catch (err) {
      console.error('Error picking or processing file:', err);
      Alert.alert('Ett fel uppstod', 'Kunde inte välja eller bearbeta filen.');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <View style={styles.container}>
      <Text style={styles.title}>Ladda XML Ordermall</Text>
      <View style={styles.buttonContainer}>
        <Button onPress={handlePickAndStoreFileAsString}>
          <Text color="white">Välj XML-fil</Text>
        </Button>
      </View>
      <Text style={styles.infoText}>
        Välj en fil som innehåller din ordermall i XML-format.
      </Text>
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    padding: 20,
    justifyContent: 'center',
    alignItems: 'center',
  },
  title: {
    fontSize: 22,
    fontWeight: 'bold',
    marginBottom: 30,
    textAlign: 'center',
  },
  buttonContainer: {
    width: '80%',
    marginBottom: 20,
  },
  infoText: {
    textAlign: 'center',
    color: 'gray',
    fontSize: 14,
    paddingHorizontal: 20,
  },
});
