import AsyncStorage from '@react-native-async-storage/async-storage';
import moment from 'moment';
import { Box, Button, HStack, Text, VStack, Center, Pressable, NativeBaseProvider } from 'native-base';
import Ionicons from '@expo/vector-icons/Ionicons';
import React, { useEffect, useState } from 'react';
import { Alert } from 'react-native';
import * as MailComposer from 'expo-mail-composer';
import * as FileSystem from 'expo-file-system';
import ListScans from './ListScans';
import Scanner from './Scanner';
import { OrderItem } from '../types/OrderItem';
import * as XLSX from 'xlsx';
import SettingsView from './SettingsView'

const App = () => {
  const [order, setOrder] = useState<OrderItem[]>([]);
  const [latestScan, setLatestScan] = useState<OrderItem | null>(null);
  const [scene, setScene] = useState('scan');
  const [settings, setInMemorySettings] = useState({mode: 'excel', xmlTemplate: null});

    const setSettings = (newValue) => {
      setInMemorySettings(newValue);
      const jsonValue = JSON.stringify(newValue);
      AsyncStorage.setItem('settings', jsonValue)
        .then(() => {
          setInMemorySettings(newValue);
        })
        .catch(() => {
          Alert.alert(
            'Fel inträffade',
            'Kunde inte spara inställning.',
            [{ text: 'Okej', style: 'cancel' }],
          );
        });
    };

  const updateStorage = (newValue: OrderItem[]) => {
    const jsonValue = JSON.stringify(newValue);
    AsyncStorage.setItem('test', jsonValue)
      .then(() => {
        setOrder(newValue);
      })
      .catch(() => {
        Alert.alert(
          'Fel inträffade',
          'Kunde inte spara aktuell order till minnet. Undvik att stänga ner appen.',
          [{ text: 'Okej', style: 'cancel' }],
        );
      });
  };

  useEffect(() => {
    AsyncStorage.getItem('test')
      .then(jsonValue => {
        if (jsonValue != null) {
          setOrder(JSON.parse(jsonValue));
        }
      })
      .catch(() => {
        Alert.alert(
          'Fel inträffade',
          'Lyckades inte ladda in historik från minnet. Dubbelkolla ifall någon artikel har försvunnit i "Lista"',
          [{ text: 'Okej', style: 'cancel' }],
        );
      });
  }, []);


  const createExcelFile = async () => {
    const worksheetData = [
      [
        'Kund',
        'Skruvab art nr',
        'Kund art nr',
        'Benämning',
        'Antal',
        'Plats',
        'Datum',
        'Övrigt',
      ],
      ...order.map(item => [
        item.customer,
        item.skruvabArticleNumber,
        item.customerArticleNumber,
        item.name,
        item.amount * item.amountMultiplier,
        item.location,
        moment(item.timestamp).format('YYYY-MM-DD HH:mm'),
        item.misc,
      ]),
    ];

    const worksheet = XLSX.utils.aoa_to_sheet(worksheetData);
    const workbook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(workbook, worksheet, 'Order');

    const fileUri = FileSystem.cacheDirectory + 'order.xlsx';
    const xlsxData = XLSX.write(workbook, { type: 'base64' });

    await FileSystem.writeAsStringAsync(fileUri, xlsxData, {
      encoding: FileSystem.EncodingType.Base64,
    });

	return fileUri;
  }

  const createMonitorXmlFile = async () => {
	const xmlString = await AsyncStorage.getItem('@RawXMLTemplateString');
	if (!xmlString) {
		Alert.alert('Varning', 'Ingen ordermall uppsatt. Ladda upp xml-mall under kugghjulet');
	}

	const orderRows = order.map((orderItem, index) => {
		return `<Row RowNumber="${(index+1)*10}" RowType="1">
          <Part PartNumber="${orderItem.skruvabArticleNumber}" SupplierPartNumber="${orderItem.customerArticleNumber}" />
          <Text>${orderItem.name}</Text>
          <ReferenceNumber />
          <Quantity>${orderItem.amount * orderItem.amountMultiplier}</Quantity>
          <Unit>st.</Unit>
          <DeliveryPeriod>${moment(orderItem.timestamp).format('YYYY-MM-DD')}</DeliveryPeriod>
          <Each />
          <Discount>0.00</Discount>
          <Setup>0.00</Setup>
          <Alloy>0.00</Alloy>
        </Row>
		`;
	})
	const orderString = xmlString
		.replace('<REPLACE_THIS/>', orderRows)
		.replace('<CUSTOMER_INVOICE_CODE/>', order[0].customer)
    const fileUri = FileSystem.cacheDirectory + 'order.xml';
    try {
		await FileSystem.writeAsStringAsync(fileUri, orderString);
	} catch (error) {
		Alert.alert('Varning', 'Misslyckades med att skapa temporär orderfil.');
	}

    return fileUri;
  }

  const handleSendMail = async () => {
	const fileUri = await createMonitorXmlFile();

	try {
		const result = await MailComposer.composeAsync({
          recipients: ['order@skruvab.com'],
          subject: 'Order',
          body: 'Se bifogad fil för order.',
          attachments: [fileUri],
        });

        if (result.status !== MailComposer.MailComposerStatus.SENT) {
          Alert.alert('Varning', 'Misslyckades med att skicka email');
        }
	} catch(error) {
          Alert.alert('Varning', 'Misslyckades med att skicka order');
	}
  };

  const confirmDelete = () => {
    Alert.alert(
      'Ta bort',
      'Du är påväg att ta bort alla skannade artiklar.\n\nÄr du säker på att du vill ta bort alla skannade artiklar?',
      [
        { text: 'Avbryt', style: 'cancel' },
        { text: 'Ja, ta bort', onPress: () => setOrder([]) },
      ],
    );
  };

  return (
    <NativeBaseProvider>
      <Box flex={1} safeArea>
        <Box flex={1}>
          {scene === 'scan' && (
            <Scanner
              latestScan={latestScan}
              setLatestScan={setLatestScan}
              order={order}
              setOrder={updateStorage}
            />
          )}
          {scene === 'list' && (
            <ListScans order={order} setOrder={updateStorage} />
          )}
          {scene === 'send' && (
            <Center flex={1}>
              <VStack space={4} w="250px">
                <Button onPress={handleSendMail}>
                  <Text color="white">Skicka order på mail</Text>
                </Button>
                <Button onPress={confirmDelete}>
                  <Text color="white">Ta bort nuvarande order</Text>
                </Button>
              </VStack>
            </Center>
          )}
          {scene === 'xmlTemplate' && (
            <SettingsView settings={settings} setSettings={setSettings}/>
          )}
        </Box>

        <HStack bg="white" alignItems="center" safeAreaBottom shadow={6}>
          <Pressable
            flex={1}
            py="3"
            onPress={() => {
              setLatestScan(null);
              setScene('scan');
            }}
          >
            <Center>
              <Text color={scene === 'scan' ? 'primary.600' : 'gray.500'}>Skanna</Text>
            </Center>
          </Pressable>
          <Pressable
            flex={1}
            py="3"
            onPress={() => setScene('list')}
          >
            <Center>
              <Text color={scene === 'list' ? 'primary.600' : 'gray.500'}>Lista</Text>
            </Center>
          </Pressable>
          <Pressable
            flex={1}
            py="3"
            onPress={() => setScene('send')}
          >
            <Center>
              <Text color={scene === 'send' ? 'primary.600' : 'gray.500'}>Skicka</Text>
            </Center>
          </Pressable>
          <Pressable
            flex={1}
            py="3"
            onPress={() => setScene('xmlTemplate')}
            >
              <Center>
                <Ionicons color={scene === 'xmlTemplate' ? '#0891b2' : '#78716c'} name="settings" size={24} />
              </Center>
          </Pressable>
        </HStack>
      </Box>
    </NativeBaseProvider>
  );
};

export default App;
