'use strict';

import React from 'react';
import { Alert, FlatList } from 'react-native';
import moment from 'moment';
import { Box, Button, VStack, HStack, Text, Pressable, Icon } from 'native-base';
import { Ionicons } from '@expo/vector-icons';
import { OrderItem } from '../types/OrderItem';

interface ListScansProps {
  order: OrderItem[];
  setOrder: (order: OrderItem[]) => void;
}

interface ValuePresenterProps {
  title: string;
  value: string | number;
}

const ValuePresenter: React.FC<ValuePresenterProps> = ({ title, value }) => (
  <HStack space={2} alignItems="center" justifyContent="space-between">
    <Text fontWeight="medium">{title}</Text>
    <Text>{value}</Text>
  </HStack>
);

const ListScans: React.FC<ListScansProps> = ({ order, setOrder }) => {
  if (order.length === 0) {
    return (
      <Box flex={1} justifyContent="center" alignItems="center" p={5}>
        <Text fontSize="xl" textAlign="center">
          Här var det tomt.{'\n'}Börja skanna artiklar under fliken 'Skanna'.
        </Text>
      </Box>
    );
  }

  const sortedOrder = [...order].sort((a, b) => b.timestamp - a.timestamp);

  const renderItem = ({ item, index }: { item: OrderItem; index: number }) => (
    <Box m={2} bg="white" rounded="lg" shadow={2}>
      <Box p={4}>
        <HStack justifyContent="space-between" alignItems="flex-start">
          <VStack space={3} flex={1}>
            <ValuePresenter title="Kund" value={item.customer} />
            <ValuePresenter title="Skruvab art nr" value={item.skruvabArticleNumber} />
            <ValuePresenter title="Benämning" value={item.name} />
            <ValuePresenter title="Hyllplats" value={item.location} />
            <ValuePresenter title="Övrigt" value={item.misc || '-'} />
            <ValuePresenter
              title="Antal"
              value={`${item.amount * item.amountMultiplier} (${item.amount} * ${item.amountMultiplier})`}
            />
            <ValuePresenter
              title="Datum"
              value={moment(item.timestamp).format('YYYY-MM-DD HH:mm')}
            />
          </VStack>
          <Pressable
            onPress={() => {
              Alert.alert(
                'Ta bort',
                'Är du säker på att du vill ta bort denna artikeln?',
                [
                  {
                    text: 'Avbryt',
                    style: 'cancel',
                  },
                  {
                    text: 'Ja, ta bort',
                    onPress: () => {
                      setOrder(order.filter((_, orderIndex) => orderIndex !== index));
                    },
                  },
                ],
              );
            }}
          >
            <Icon
              as={Ionicons}
              name="close-circle-outline"
              size="lg"
              color="gray.500"
            />
          </Pressable>
        </HStack>
      </Box>
    </Box>
  );

  return (
    <FlatList
      data={sortedOrder}
      keyExtractor={(_, index) => index.toString()}
      renderItem={renderItem}
      contentContainerStyle={{ padding: 8 }}
    />
  );
};

export default ListScans;
